<template>
    <div class="admin">
        <div class="content">
            <div class="welcome">欢迎光临六点钟餐饮</div>
            <router-view/>
        </div>
    </div>
</template>

<script>
import Login from "./admin/Login";
import Register from "./admin/Register";
export default {
    name: "admin",
    components: {
        Login,
        Register
    }
};
</script>

<style scoped>
@media (max-width: 1204px) {
    .content {
        padding: 4em 0 3em 0;
    }
    .welcome {
        width: 80%;
        height: 3em;
        margin: 1em auto;
        text-align: center;
        font-size: 1.5em;
    }
}
</style>


